html {
  background: #e7e7e7;
}

body {
  -webkit-user-select: none;
  user-select: none;
}

#question {
  width: 50%;
  justify-content: space-evenly;
  margin: auto;
  display: flex;
}

.answer-btn {
  width: 100%;
  height: 50px;
  text-align: center;
  cursor: pointer;
  border: 2px solid #9e9e9e;
  border-radius: 8px;
  margin: 4px;
  padding: 8px;
  font-size: 150%;
  line-height: 50px;
  transition: background-color .5s;
}

.answer-btn:hover {
  background-color: #9e9e9e;
}

.right-answer, .right-answer:hover {
  background-color: #68f08a;
}

.piano > li {
  box-sizing: border-box;
  float: left;
  margin: 0;
  padding: 0;
  list-style: none;
  position: relative;
}

.piano > .white {
  height: 9.36em;
  width: 1.95em;
  z-index: 1;
  background: #f3f3f3;
  border: 1px solid #838383;
}

.piano > .black {
  height: 6.24em;
  width: 1.3em;
  z-index: 2;
  background: #000;
  border: 1px solid #000;
  margin: 0 0 0 -.65em;
}

.piano > .active {
  background: gray;
  border: 1px solid gray;
}

.piano > .b, .piano > .e, .piano > .g, .piano > .d, .piano > .a {
  margin: 0 0 0 -.65em;
}

.piano > .white[data-note="c4"] {
  background-color: #ccc;
}

.piano > .white:hover, .piano > .black:hover {
  background: gray;
  border: 1px solid gray;
}

.piano > .highlight {
  background-color: #7fc1ec !important;
}

